

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.star {
    cursor: pointer;
    height: 25px;
    width: 25px;
    margin: 2px;
    float: left;
    background-color: grey;
    -webkit-clip-path: polygon(
        50% 0%,
        63% 38%,
        100% 38%,
        69% 59%,
        82% 100%,
        50% 75%,
        18% 100%,
        31% 59%,
        0% 38%,
        37% 38%
    );
            clip-path: polygon(
        50% 0%,
        63% 38%,
        100% 38%,
        69% 59%,
        82% 100%,
        50% 75%,
        18% 100%,
        31% 59%,
        0% 38%,
        37% 38%
    );
}
.star.selected {
  background-color: red;
}
#close {
	position: absolute;
}


.color-container {
	height: 300px;
	width: 320px;
	border: 2px solid rgba(35,70,140,0.7);
	border-radius: 5px;
	background: rgba(128, 128, 128, 0.17);
	margin: 5px 10px;
	display: inline-block;
}
.color-name{
	height: 30%;
	font-weight: bold;
	font-size: 1.5em;
	padding: 30px;
	text-align: center;
}
.color {
	height: 40%;
	background-color: blue; 
}
.star-rating {
	padding: 30px;
	height: 30%;
	text-align: center;
}

@media (min-width: 992px) {
	.color-container {
		height: 350px;
		width: 320px;
	}	
}

@media (min-width: 768px){
	.color-container {
		height: 320px;
		width: 320px;
	}	
	.color-name{
		height: 30%;
		font-weight: bold;
		font-size: 1.5em;
		padding: 30px;
		text-align: center;
	}
}



